import React from 'react';
import styled from 'styled-components';

const GovernorDetails = ({ clicked, governor }) => {

    const handleClick = () => {
        console.log('handleClick');
        clicked({
            first: governor.first_name,
            last: governor.last_name
        });
    }
    console.log("governor");
    console.log(governor);
    const party = governor.party === 'republican' ? 'R-' : governor.party === 'democrat' ? 'D-' : 'I-';
    const partyState = '(' + party + governor.state_code + ')';

    return (
        <GovernorWrapper onClick={handleClick}>
            {governor && governor.party === 'democrat' &&
                <Democrat>
                    <div className='top'>
                    <img width="40%" src={governor.photo_url} alt='governor' />
                        <span>
                        {governor.first_name} {governor.last_name}
                            <br />
                            {partyState}
                        </span>
                    </div>
                <div className='bio'>{governor.biography}</div>
                </Democrat>
            }
            {governor && governor.party === 'republican' &&
                <Republican>
                    <div className='top'>
                        <span>
                        {governor.first_name} {governor.last_name}
                            <br />
                            {partyState}
                        </span>
                    <img width="40%" src={governor.photo_url} alt='governor' />
                    </div>
                <div className='bio'>{governor.biography}</div>
                </Republican>
            }
            {governor && governor.party === 'independent' &&
                <Independent>
                    <div className='top'>
                    <img width="40%" src={governor.photo_url} alt='governor' />
                        <span>
                        {governor.first_name} {governor.last_name}
                            <br />
                            {partyState}
                        </span>
                    </div>
                <div className='bio'>{governor.biography}</div>
                </Independent>
            }
            {!governor && <NoData>
                <span>No Governor Data!</span>
            </NoData>}
        </GovernorWrapper>
    );
};

const BaseGovernor = styled.div`
    width: 100%;
    padding: 1.5rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    font-size: 1.1rem;
    .top {
        display: flex;
        justify-content: space-around;
        margin-bottom: 1rem;
    }
    /* .bio {
        height: 30%;
        overflow-y: scroll;
    } */
`;

const Republican = styled(BaseGovernor)`
    background-color: rgba(255, 0, 0, 0.5);
    text-align: right;
`;

const Independent = styled(BaseGovernor)`
    background-color: rgba(255, 255, 0, 0.5);
    text-align: center;
`;

const Democrat = styled(BaseGovernor)`
    background-color: rgba(0, 0, 255, 0.5);
    text-align: left;
`;

const NoData = styled.div`
    padding: 2rem;
    background-color: #333;
    color: white;
    border-radius: 2rem;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
`;

const GovernorWrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    background-color: #999;
    border: 1px solid blue;
    border-radius: 13px;
    padding: 0.5rem;
    margin: 2rem auto;
`;

export default GovernorDetails;



