import React from 'react';
import styled from 'styled-components';

const Senator = ({ clicked, senator }) => {
    if (!senator) { return (<div></div>); }

    const handleClick = () => {
        console.log('handleClick');
        clicked(senator);
    }
    const party = senator.party === 'republican' ? 'R-' : senator.party === 'democrat' ? 'D-' : 'I-';
    const partyState = '(' + party + '-' + senator.state_code + ')';

    return (
        <SenatorWrapper onClick={() => handleClick()}>
            { senator && senator.party === 'democrat' &&
                <Democrat>
                <div>
                    <span>{senator.first_name} {senator.last_name} {partyState}</span>
                    <p>Class {senator.class} - Term Ends {senator.term_end}</p>
                </div>
                <img width="20%" src={senator.photo_url} alt='senator_photo' />
                </Democrat>
            }
            { senator && senator.party === 'republican' &&
                <Republican>
                <img width="20%" src={senator.photo_url} alt='senator_photo' />
                <div>
                    <span>{senator.first_name} {senator.last_name} {partyState}</span>
                    <p>Class {senator.class} - Term Ends {senator.term_end}</p>
                </div>
                </Republican>
            }
            { senator && senator.party === 'independent' &&
                <Independent>
                <img width="20%" src={senator.photo_url} alt='senator_photo' />
                <div>
                    <span>{senator.first_name} {senator.last_name} {partyState}</span>
                    <p>Class {senator.class} - Term Ends {senator.term_end}</p>
                </div>
                </Independent>
            }
            {!senator && <NoData>
                <span>No Senator Data!</span>
            </NoData>}
        </SenatorWrapper>
    );
};

const BaseSenator = styled.div`
    width: 100%;
    padding: 1rem;
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1rem;
    color: #ccc;
    text-shadow: 2px 2px black;
    font-weight: bold;
`;

const Republican = styled(BaseSenator)`
    background-color: rgba(255, 0, 0, 0.5);
    text-align: right;
`;

const Independent = styled(BaseSenator)`
    background-color: rgba(255, 255, 0, 0.5);
    text-align: center;
`;

const Democrat = styled(BaseSenator)`
    background-color: rgba(0, 0, 255, 0.5);
    text-align: left;
`;

const NoData = styled.div`
    padding: 2rem;
    background-color: #333;
    color: white;
    border-radius: 2rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
`;

const SenatorWrapper = styled.div`
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    background-color: #999;
    border: 1px solid blue;
    border-radius: 13px;
    padding: 0.5rem;
    margin: 1rem auto;
    :hover {
        border: 1px solid rgba(0, 128, 0, 0.5);
        cursor: pointer;
    }
`;

export default Senator;



