import React from 'react';
import styled from 'styled-components';
import Signin from '../components/auth/signin';
import Signup from '../components/auth/signup';
import {SignupSenators} from '../components/senator';

const Auth = () => {
    const [showRegister, setShowRegister] = React.useState(false);
    const [userState, setUserState] = React.useState(null);

    const showReg = () => {
        setShowRegister(true);
    };

    const showSig = () => {
        setShowRegister(false);
    };

    return (
        <AuthWrapper>
            <FormWrapper>
                <AuthNavWrapper>
                    <NavButton className={`${showRegister ? '' : 'active'}`} onClick={showSig}>Sign In</NavButton>
                    <NavButton className={`${showRegister ? 'active' : ''}`} onClick={showReg}>Sign Up</NavButton>
                </AuthNavWrapper>
                <ContentPage>
                    {showRegister && <Signup setUserState={setUserState} />}
                    {!showRegister && <Signin />}
                </ContentPage>
            </FormWrapper>
            <SideWrapper>
                {userState && <SignupSenators userState={userState} />}
            </SideWrapper>
        </AuthWrapper>
    );
};

const ContentPage = styled.div`
    margin: 0 auto;
`;

const SideWrapper = styled.div`
    width: 50%;
    overflow: hidden;
    height: 70vh;
    border-radius: 2rem;
    background-color: #777;
`;

const FormWrapper = styled.div`
    width: 45%;
`;

const NavButton = styled.span`
    padding: 0.5rem;
    color: #fff;
    font-size: 1.25rem;
    cursor: pointer;
`;

const AuthNavWrapper = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .active {
        font-size: 2rem;
        font-weight: bolder;
        color: #0ff;
        text-shadow: 1px 1px #000;
    }
`;

const AuthWrapper = styled.div`
    width: 99%;
    display: flex;
    align-items: top;
    justify-content: space-between;
    padding: 2rem;
    border-radius: 4rem;
    margin: 2rem auto;
    color: white;
    background-color: #999;
`;

export default Auth;



