import React from 'react';
import styled from 'styled-components';

export const Loading = () => {

    return (
        <LoadingWrapper>

        </LoadingWrapper>
    );
};

const LoadingWrapper = styled.div`
    width: 100%;
    height: 100%;
    background-color: orange;
`;

export default Loading;



