import {
    GET_SENATORS_SUCCESS
} from '../actions/types';

const senatorsString = localStorage.getItem("senators");

let senators;
if (senatorsString) {
    senators = JSON.parse(senatorsString);
}

const initialState = senators
    ? { senators: senators }
    : { senators: null };

export const reducer = (state = initialState, action) => {

    switch (action.type) {
        case GET_SENATORS_SUCCESS:
            return { ...state, senators: action.payload.senators }
        default:
            return state;
    }
};



