import {
    GET_REPRESENTATIVES_SUCCESS
} from '../actions/types';

const representativesString = localStorage.getItem("representative");

let representatives;
if (representativesString) {
    representatives = JSON.parse(representativesString);
}

const initialState = representatives
    ? { representatives: representatives }
    : { representatives: null };

export const reducer = (state = initialState, action) => {

    switch (action.type) {
        case GET_REPRESENTATIVES_SUCCESS:
            return { ...state, representatives: action.payload.representatives }
        default:
            return state;
    }
};



