import {
    GET_STATES_SUCCESS
} from '../actions/types';

// const senatorsString = localStorage.getItem("states");

// let states;
// if (senatorsString) {
//     states = JSON.parse(senatorsString);
// }

const initialState = null;//states
    // ? { states: states }
    // : { states: null };

export const reducer = (state = initialState, action) => {

    switch (action.type) {
        case GET_STATES_SUCCESS:
            return { ...state, states: action.payload.states }
        default:
            return state;
    }
};



