import React from 'react';
import styled from 'styled-components';

const SenatorDetails = ({ clicked, senator }) => {

    const handleClick = () => {
        console.log('handleClick');
        clicked({
            first: senator.first_name,
            last: senator.last_name
        });
    }
    console.log("senator");
    console.log(senator);
    const party = senator.party === 'republican' ? 'R-' : senator.party === 'democrat' ? 'D-' : 'I-';
    const partyState = '(' + party + senator.state_code + ')';

    return (
        <SenatorWrapper onClick={handleClick}>
            { senator && senator.party === 'democrat' &&
                <Democrat>
                    <div className='top'>
                        <img width="40%" src={senator.photo_url} alt='senator' />
                        <span>
                            {senator.first_name} {senator.last_name}
                            <br />
                            {partyState}
                        </span>
                    </div>
                    <div className='bio'>{senator.biography}</div>
                </Democrat>
            }
            { senator && senator.party === 'republican' &&
                <Republican>
                    <div className='top'>
                        <span>
                            {senator.first_name} {senator.last_name}
                            <br />
                            {partyState}
                        </span>
                        <img width="40%" src={senator.photo_url} alt='senator' />
                    </div>
                    <div className='bio'>{senator.biography}</div>
                </Republican>
            }
            { senator && senator.party === 'independent' &&
                <Independent>
                    <div className='top'>
                        <img width="40%" src={senator.photo_url} alt='senator' />
                        <span>
                            {senator.first_name} {senator.last_name}
                            <br />
                            {partyState}
                        </span>
                    </div>
                    <div className='bio'>{senator.biography}</div>
                </Independent>
            }
            {!senator && <NoData>
                <span>No Senator Data!</span>
            </NoData>}
        </SenatorWrapper>
    );
};

const BaseSenator = styled.div`
    width: 100%;
    padding: 1.5rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    font-size: 1.1rem;
    .top {
        display: flex;
        justify-content: space-around;
        margin-bottom: 1rem;
    }
    /* .bio {
        height: 30%;
        overflow-y: scroll;
    } */
`;

const Republican = styled(BaseSenator)`
    background-color: rgba(255, 0, 0, 0.5);
    text-align: right;
`;

const Independent = styled(BaseSenator)`
    background-color: rgba(255, 255, 0, 0.5);
    text-align: center;
`;

const Democrat = styled(BaseSenator)`
    background-color: rgba(0, 0, 255, 0.5);
    text-align: left;
`;

const NoData = styled.div`
    padding: 2rem;
    background-color: #333;
    color: white;
    border-radius: 2rem;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
`;

const SenatorWrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    background-color: #999;
    border: 1px solid blue;
    border-radius: 13px;
    padding: 0.5rem;
    margin: 2rem auto;
`;

export default SenatorDetails;



