import {
    GET_GOVERNORS_SUCCESS
} from '../actions/types';

const governorsString = localStorage.getItem("governors");

let governors;
if (governorsString) {
    governors = JSON.parse(governorsString);
}

const initialState = governors
    ? { governors: governors }
    : { governors: null };

export const reducer = (state = initialState, action) => {

    switch (action.type) {
        case GET_GOVERNORS_SUCCESS:
            return { ...state, governors: action.payload.governors }
        default:
            return state;
    }
};



