import React from 'react';
import { useSelector } from 'react-redux';
import {getSenators} from '../../actions';
import styled from 'styled-components';
import Senator from './sen';

export const SignupSenators = ({ userState }) => {

    const [userSenators, setUserSenators] = React.useState();
    getSenators('all');
    const senators = useSelector(state => state.senators.senators);
    React.useEffect(() => {
        if (userState) {
            senators && setUserSenators(senators.filter(s => {
                return s.state_code === userState;
            }));
        };
    }, [userState]);

    return (
        <SignupSenatorsWrapper>
            <CenterContent>
                <h3>Your senators</h3>
                {userSenators && <div>
                    <SenatorTop><Senator senator={userSenators[0]} /></SenatorTop>
                    <SenatorBottom><Senator senator={userSenators[1]} /></SenatorBottom>
                </div>}
            </CenterContent>
        </SignupSenatorsWrapper>
    );
    
}

const CenterContent = styled.div`
    width: 40%;
    margin: 0 auto;
`;

const SenatorTop = styled.div`
    background-color: #aaa;
`;

const SenatorBottom = styled.div`
    background-color: #aaa;
`;

const SignupSenatorsWrapper = styled.div`
    margin-top: 3rem;
    width: 100%;
    text-align: center;
`;



